import { ToastService } from './../../../services/toast.service';

import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { environment } from 'src/environments/environment.prod';
import { PreviewAnyFile } from '@ionic-native/preview-any-file/ngx';

@Component({
  selector: 'app-moksh-pro',
  templateUrl: './moksh-pro.page.html',
  styleUrls: ['./moksh-pro.page.scss'],
})
export class MokshProPage implements OnInit {
  
  arrServices = [];
  webAssetsPath = "";
  currentSelected=0;
  SubSelectedTab="";

  arrVideos = [];
  isVideoOpen : boolean ;
  iframe : any;  
  slideVideoOpts = {
    slidesPerView: 1.5    
  }
  
  slideOpts = {
    slidesPerView: 2.8,
    freeMode: true,
    coverflowEffect: {
      rotate: 50,
      stretch: 0,
      depth: 100,
      modifier: 1,
      slideShadows: true,
    }
  }

  constructor(
    private headerTitle: HeaderTitleService, 
    private apiGeneralService: ApiGeneralService, 
    private sanitizer : DomSanitizer,
    private toastr : ToastService,
    private previewFile: PreviewAnyFile
  ) {
    this.headerTitle.isBack = false;
    this.headerTitle.title = "MOKSH PRO";
  }

  ngOnInit() {
    this.getServices();
    this.getVideos();
  }


  getServices(){
    this.arrServices = 
    [
      {'serviceID':1,'serviceImage':'','serviceName':'PROGRAM'},
      {'serviceID':2,'serviceImage':'','serviceName':'SUITABILITY'},
      {'serviceID':3,'serviceImage':'','serviceName':'ELIGIBILITY'},
      {'serviceID':4,'serviceImage':'','serviceName':'BENEFITS'},
      {'serviceID':5,'serviceImage':'','serviceName':'SUCCESS STORY'}
    ]
  }
  ionSelected(index){    
    this.currentSelected = index;      
  }

  onClickDownload(){
    var brochureLink = environment.webAssetsPath + "mobility-app/moksh-pro/MOKSH_Pro_USMLE_Graduates.pdf";    
    this.previewFile.preview(brochureLink);
  }

  //#region  Video Section start
  async getVideos() {
    let postData = { 'videoHeaderId': 26, 'isActive':true}
    await this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.webAssetsPath = environment.webAssetsPath+ "mobility-app/videothumbnails";
      this.arrVideos = resp;
    })
      .catch(err => {
        console.log("getVideos: " + err);
      })
  }
  
  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //#endregion

}

